@import "normalizes.scss";

@font-face {
    font-family: "MontserratT";
    src: url('../font/Montserrat-Regular.ttf');
    font-weight: regular;
    font-style: normal; }

@font-face {
    font-family: "MontserratT";
    src: url('../font/Montserrat-SemiBold.ttf');
    font-weight: bold;
    font-style: normal; }

$color-blue: #5290ea;
$color-orange: #F28B64;
$color-grey: #E2E2E2;
$color-white: white;

* {
    box-sizing: border-box; }

html {
    font-family: "MontserratT";
    font-weight: regular;
    scroll-behavior: smooth; }

.container {
    max-width: 1140px;
    margin: 0 auto;
    position: relative; }

.main-section {
    position: relative;
    min-height: 800px;
    background: $color-grey;
    padding-top: 16px;
    overflow: hidden;
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 69px;
        align-items: center; }
    &__logo {
        width: 134px;
        padding-top: 16px; }
    &__content-container {
        position: relative;
        display: flex;
        flex-direction: row; }
    &__first-elipse {
        position: absolute;
        top: 129px;
        right: 33px; }
    &__second-elipse {
        position: absolute;
        right: -120px;
        top: 650px; } }


.nav-menu {
    &__list {
        display: flex;
        flex-direction: row;
        list-style-type: none; }
    &__list-item {
        margin-right: 64px;
        font-size: 18px;
        font-weight: bold;
        &:last-child {
            margin-right: 0; }
        a {
            text-decoration: none;
            color: black; } } }

.left-wrapper {
    position: relative;
    img {
        position: relative;
        z-index: 5;
        width: 883px;
        height: 709px; }
    &__rectangle-blue {
        position: absolute;
        top: 150px;
        left: -50px;
        width: 565px;
        height: 220px;
        background: $color-blue;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-64deg);
        z-index: 2; }
    &__rectangle-orange {
        position: absolute;
        width: 618.43px;
        height: 220px;
        left: -30px;
        top: 160px;
        transform: rotate(-50.81deg);
        background: $color-orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        z-index: 3; }
    &__rectangle-grey {
        position: absolute;
        width: 608.97px;
        height: 220px;
        left: 10px;
        top: 190px;
        transform: rotate(-36.15deg);
        background: $color-grey;
        box-shadow: 0px 4px 4px rgba(83, 83, 83, 0.87);
        border-radius: 100px;
        z-index: 1; } }

.right-wrapper {
    width: 372px;
    position: absolute;
    top: 150px;
    right: 76px;
    z-index: 5;
    &__icons-box {
        margin-bottom: 1px;
        padding-left: 10px; }
    &__text {
        font-size: 16px;
        margin: 0 0 20px 0; } }

.icons {
    display: flex;
    flex-direction: row;
    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        border: 1px solid $color-blue;
        border-radius: 100px;
        margin-right: 12px;
        &:last-child {
            margin-right: 0px; }
        i {
            font-size: 21px; } } }

.title {
    font-size: 72px;
    margin: 0;
    position: relative;
    font-weight: bold;
    &__hr {
        width: 65px;
        position: absolute;
        bottom: 0px;
        margin: 0 0 5px 0;
        border: none;
        border-top: 1px solid black; } }

.buttons {
    display: inline-flex;
    &__main-btn {
        margin-right: 32px;
        width: 200px;
        height: 60px;
        background: $color-orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        border: none;
        color: white;
        font-size: 18px;
        font-weight: bold; }
    &__icon-btn {
        display: inline-flex;
        img {
            width: 46px;
            height: 46px;
            margin-right: 12px; } }
    &__text {
        font-size: 16px; } }

.elipse {
    width: 194px;
    height: 194px;
    background: linear-gradient(144deg, #ED7B50 0%, #488AEA 54%, #2C2C2C 100%);
    border-radius: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    &_dig {
        width: 282px;
        height: 282px; }
    &__subtract {
        width: 129.33px;
        height: 129.33px;
        background: #F0F0F0;
        border-radius: 150px;
        &_big {
            width: 188px;
            height: 188px; }
        &_white {
            background: $color-white; } } }

.second-section {
    min-height: 700px;
    overflow: hidden;
    &__container {
        padding-top: 32px;
        position: relative; }
    &__title {
        margin-bottom: 40px; }
    &__cards-box {
        display: inline-flex;
        justify-content: center;
        margin-bottom: 25px; }
    &__cards-item {
        margin-right: 30px;
        &:last-child {
            margin-right: 0; } }
    &__icons-box {
        display: flex;
        justify-content: space-evenly; }
    &__first-elipse {
        position: absolute;
        top: 80px;
        right: -60px;
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        top: 364px;
        left: -60px;
        z-index: -1; } }


.cards-item {
    position: relative;
    width: 360px;
    height: 360px;
    background: $color-grey;
    border-radius: 10px;
    box-shadow: -1px 7px 8px -4px rgba(34, 60, 80, 0.29);
    display: flex;
    align-items: flex-end;
    &__img {
        height: 335px; }
    &__text {
        width: 280px;
        font-size: 36px;
        text-align: center;
        transform: rotate(90deg);
        position: absolute;
        top: 100px;
        right: -55px;
        font-weight: bold; } }

.cards-item_orange {
    background: $color-orange; }

.cards-item_blue {
    background: $color-blue; }

.icons-items {
    display: inline-flex;
    align-items: center;
    &__img {
        margin-right: 20px;
        width: 80px;
        height: 80px; }
    &__text {
        font-size: 18px;
        margin: 0;
        span {
            font-size: 48px;
            font-weight: bold; } } }

.third-section {
    position: relative;
    min-height: 640px;
    background: $color-grey;
    z-index: 0;
    overflow: hidden;
    &__container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 46px 20px 0 167px; }
    &__right-wrapper {
        max-width: 480px; }
    &__right-title {
        margin-bottom: 20px; }
    &__text {
        font-size: 18px;
        margin-bottom: 30px; }
    &__first-elipse {
        position: absolute;
        top: 177px;
        left: 110px;
        z-index: -2; }
    &__figure {
        width: 230px;
        height: 230px;
        position: absolute;
        top: 254px;
        left: 7px;
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        right: -120px;
        top: -120px; } }

.title__hr_top {
    bottom: 86px; }

.footer-section {
    min-height: 265px;
    &__container {
        display: flex;
        justify-content: flex-end; }
    &__img {
        width: 800px;
        height: 362px;
        position: absolute;
        left: -381px;
        bottom: -72px; }
    &__wrapper {
        padding-top: 30px; }
    &__title {
        margin-bottom: 20px; } }

@media (max-width: 1199px) {
    .container {
        width: 960px; }

    .main-section {
        min-height: 690px; }

    .left-wrapper {
        img {
            width: 750px; }
        &__rectangle-blue {
            width: 500px;
            height: 200px; }
        &__rectangle-orange {
            width: 550px;
            height: 200px; }
        &__rectangle-grey {
            width: 500px;
            height: 200px; } }

    .right-wrapper {
        top: 109px;
        right: 29px; }

    .main-section {
        &__first-elipse {
            top: 101px;
            right: 0px; } }

    .main-section {
        &__second-elipse {
            right: -119px;
            top: 642px; } }

    .second-section {
        min-height: 600px;
        &__second-elipse {
            top: 285px;
            left: -42px; }
        &__cards-box {
            display: flex;
            justify-content: center; } }

    .cards-item {
        width: 300px;
        height: 300px;
        margin-right: 20px;
        &__img {
            height: 270px; }
        &__text {
            font-size: 30px; } }

    .third-section {
        &__container {
            padding: 20px 20px 0 25px; }
        &__second-elipse {
            right: -178px;
            top: -120px; } }

    .footer-section {
        &__img {
            width: 755px;
            height: 294px;
            left: -364px;
            bottom: -19px; } } }

@media (max-width: 991px) {
    .container {
        width: 720px; }

    .main-section {
        min-height: 640px;
        &__second-elipse {
            right: -127px;
            top: 533px; } }

    .left-wrapper {
        img {
            width: 611px;
            height: 550px; }
        &__rectangle-blue {
            width: 400px;
            height: 150px; }
        &__rectangle-orange {
            width: 400px;
            height: 150px; }
        &__rectangle-grey {
            width: 400px;
            height: 150px; } }

    .right-wrapper {
        top: 62px;
        right: -104px;
        &__title {
            font-size: 50px; }
        &__text {
            width: 280px;
            font-size: 16px;
            margin: 10px 0 10px 0; }
        &__icons-box {
            margin-bottom: 11px;
            padding-left: 25px; } }

    .buttons {
        flex-direction: column;
        padding-left: 45px;
        &__main-btn {
            margin-right: 12px;
            width: 144px;
            height: 48px;
            font-size: 14px; }
        &__icon-btn {
            align-items: center;
            padding-top: 10px; } }

    .second-section {
        min-height: 430px;
        &__container {
            padding-top: 10px; }
        &__title {
            margin-bottom: 15px;
            font-size: 50px; }
        &__cards-box {
            margin-bottom: 5px; }
        &__cards-item {
            width: 250px;
            height: 250px;
            margin-right: 10px; } }

    .cards-item__text {
        font-size: 25px;
        width: 225px;
        top: 82px; }

    .cards-item__img {
        height: 230px; }

    .icons-items__img {
        margin-right: 10px;
        width: 60px;
        height: 60px; }

    .icons-items__text {
        font-size: 14px; }

    .icons-items__text span {
        font-size: 34px; }

    .third-section {
        &__right-wrapper {
            padding-left: 50px; }
        &__left-title {
            font-size: 50px; }
        &__right-title {
            font-size: 50px; }
        &__text {
            margin-bottom: 10px; } }

    .title__hr_top {
        bottom: 56px; }

    .footer-section {
        min-height: 230px;
        &__title {
            margin-bottom: 5px;
            font-size: 50px; }
        &__img {
            width: 685px;
            height: 261px;
            left: -364px;
            bottom: -47px; } } }


@media (max-width: 767px) {
    .container {
        width: 540px; }
    .nav-menu {
        position: fixed;
        top: 0;
        right: -100%;
        height: 100vh;
        background: $color-blue;
        z-index: 20;
        width: 50%;
        transition: 0.6s all;
        &__list {
            flex-direction: column;
            justify-content: space-evenly;
            align-items: center;
            height: 100%;
            padding-left: 0; }
        &__list-item {
            margin-right: 0;
            font-size: 20px; } }
    .hamburger {
        position: absolute;
        width: 30px;
        top: 40px;
        right: 30px;
        height: 14px;
        z-index: 30;
        span {
            display: block;
            width: 100%;
            height: 2px;
            background: black;
            margin-bottom: 4px;
            transition: 1s all;
            &:last-child {
                margin-bottom: 0; } } }
    .hamburger_active {
        span {
            margin-bottom: -2px;
            &:nth-child(1) {
                transform: translateY(3px) rotate(-45deg); }
            &:nth-child(2) {
                display: none; }
            &:nth-child(3) {
                transform: translateY(3px) rotate(45deg); } } }
    .nav-menu_active {
        right: 0px; }

    .left-wrapper {
        img {
            width: 425px;
            height: 356px; }
        &__rectangle-blue {
            width: 312px;
            height: 120px;
            top: 49px;
            left: -8px; }
        &__rectangle-orange {
            width: 350px;
            height: 142px;
            left: -6px;
            top: 45px; }
        &__rectangle-grey {
            width: 347px;
            height: 125px;
            left: 15px;
            top: 74px; } }

    .main-section {
        min-height: 440px;
        &__logo {
            width: 90px;
            padding-top: 0; }
        &__first-elipse {
            display: none; }
        &__second-elipse {
            display: none; } }

    .left-wrapper {
        img {
            width: 425px;
            height: 356px; }
        &__rectangle-blue {
            width: 312px;
            height: 120px;
            top: 49px;
            left: -8px; }
        &__rectangle-orange {
            width: 350px;
            height: 142px;
            left: -6px;
            top: 45px; }
        &__rectangle-grey {
            width: 347px;
            height: 125px;
            left: 15px;
            top: 74px; } }

    .right-wrapper {
        top: 40px;
        right: -104px;
        width: 302px;
        &__icons-box {
            margin-bottom: 5px;
            padding-left: 10px;
            margin-right: 0; }
        &__icon-btn {
            padding-left: 20px; }
        &__text {
                width: 208px;
                font-size: 15px;
                margin: 10px 0 10px 0; } }
    .title {
        font-size: 40px;
        padding-left: 8px;
        &__hr {
            width: 37px;
            margin: 0; } }
    .icons {
        &__item {
            margin-right: 12px;
            width: 35px;
            height: 35px; } }
    .buttons {
        &__main-btn {
            margin: 0 5px 5px 0;
            width: 140px;
            height: 40px;
            font-size: 14px; }
        &__icon-btn {
            img {
                width: 30px;
                height: 30px; } }
        &__text {
            font-size: 14px; } }

    .second-section {
        &__container {}
        &__title {
            margin-bottom: 10px; }
        &__cards-item {
            width: 200px;
            height: 200px; }
        &__first-elipse {
            display: none; }
        &__second-elipse {
            display: none; } }

    .cards-item {
        &__img {
            height: 185px; }
        &__text {
            font-size: 20px;
            width: 177px;
            top: 62px; } }

    .icons-items {
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;
        padding-top: 20px;
        &__img {
            width: 45px;
            height: 45px; }
        &__text {
            padding: 10px 0 0 30px;
            span {
                font-size: 20px; } } }


    .third-section {
        min-height: 600px;
        &__container {
            padding: 25px 10px 0 10px; }
        &__left-wrapper {
            display: none; }
        &__first-elipse {
            display: none; }
        &__second-elipse {
            display: none; }
        &__figure {
            display: none; } }

    .title {
        &__hr {
            &_top {
                bottom: 50px; } } }


    .footer-section {
        min-height: 175px;
        &__container {
            width: 353px; }
        &__img {
            display: none; }
        &__wrapper {
            padding-top: 15px; }
        &__icons-box {
            padding-left: 15px; } } }


@media (max-width: 575px) {
    .container {
        width: 100%; } }









